import None from "@/components/qy/none.vue"
export default {
	data() {
		return {
			c_index:0,
			list:[],
			class_name: 'cou_t_l_01',
			list_empty: false,
		};
	},
	components:{
		None
	},
	onLoad(options) {
		this._load()
	},
	methods:{
		_load() {
			this.$api.http.get('coupon/get_coupon').then((res)=>{
				if (res.data=='') {
					this.list_empty = true
				} else {
					this.list = res.data
				}
				uni.stopPullDownRefresh();
			});
		},
		get_coupon(id){
			this.$api.http.get('coupon/add_coupon',{id:id}).then(res=>{
				this.$api.msg('领取成功')
				this._load();
			})
		},
		
	},
	onPullDownRefresh() {
		this._load();
		setTimeout(function () {
			uni.stopPullDownRefresh();
		}, 2000);
	}
}
